@import url('https://fonts.googleapis.com/css?family=Permanent+Marker&display=swap');

.nav-bar{
    background: linear-gradient(to top, #141e30);
    height: 50px;
    margin-right: 1%;
    margin-bottom: 2.1%;
}

.nav-btn {
    background-color: white;
    float: right;
    font-family: Arial, Helvetica, sans-serif;
    font-size: 15px;
    border-color: black;
    transform: skewX(-10deg);
    margin-left: 0.04%;
    height: 50px;
    width: 100px;
    margin-right: 0%;
    transition: all 0.2s ease-in-out;
}

.nav-btn:hover{
    background-color: rgb(228, 228, 228);
    border-radius: 15px;
}


.active {
    border-radius: 15px;
    background-color: rgb(228, 228, 228);

}

@media (max-width: 768px) {
    .nav-btn{
        height: 35px;
        width: 70px;
        font-size: 12px;
    }
    .active {
        background-color: rgb(228, 228, 228);
        border-radius: 15px;
    }

    
}

@media (min-width: 768px) {
    .logo-2{
        font-size: 24px;
    }
}

@media (max-width: 500px) {
 .logo-2{
     font-size: 15px;
 }
}
@media (max-width: 465px) {
 .logo-2{
     display: none;
 }
}

.logo-2{
    float: left;
    margin-top: 1%;
    margin-left: 1.5%;
    font-family: 'Permanent Marker', cursive;
    letter-spacing: 1.5px;
    border: solid white 1px;
    padding: 2px 6px 1px;
    border-radius: 12px;
}